@import '../../../../../style/variable.scss';

.infoblock {
  background: var(--poblick);
  width: 100%;

  &__items {
    display: flex;
    width: 100%;
    flex-direction: column;
  }

  &__item {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    height: 500px;
  }

  &__block1 {
    width: 50%;
    height: 100%;
    overflow: hidden;
    border-radius: 2px;
    position: relative;
  }

  &__back {
    width: 5%;
    height: 0%;
    position: absolute;
    background: var(--gray);
    opacity: 0;
    transition: width 0.5s ease 0.5s, height 0.5s ease 0s, opacity 0.5s ease 0s;
    left: 0;
  }
  &__item.show &__back {
    height: 100%;
    width: 100%;
    opacity: 1;
  }

  &__img {
    left: 0;
    position: absolute;
    width: 0%;
    height: 100%;
    background-image: url('../../../../../img/Home/infoBlock/1.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transition: width 0.5s ease 1s;
  }

  &__item.show &__img {
    width: 100%;
  }

  &__block2 {
    width: 50%;
    display: flex;
    justify-content: center;
    color: var(--searchBlock);
    opacity: 0;
    transition: opacity 0.5s ease 0.5s;
  }
  &__body {
    width: 55%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    color: var(--searchBlock);
  }
  &__item.show &__block2 {
    opacity: 1;
  }
  &__block2 h2 {
    padding-bottom: 10px;
  }
  &__back.two {
    right: 0;
    left: auto;
  }
  &__item:last-child &__img {
    background-image: url('../../../../../img/Home/infoBlock/2.jpg');
    right: 0;
    left: auto;
  }
  @media (max-width: $mw2 + px) {
    &__item {
      height: 400px;
    }
  }
  @media (max-width: $mw3 + px) {
    &__body {
      width: 100%;
    }
    h2 {
      text-align: center;
    }
    &__item {
      height: auto;
      display: grid;
      grid-template: 250px 1fr / 1fr;
      width: 100%;
    }
    &__block1 {
      width: 100%;
    }
    &__block2 {
      margin-top: 15px;
      margin-bottom: 30px;
      width: 100%;
      margin-right: 0px !important;
    }
    &__item:last-child &__block2 {
      margin-bottom: 0px;
    }
    &__item:last-child &__block2 {
      order: 1;
    }
    &__item:last-child {
      margin-bottom: 0px;
    }
  }
  @media (max-width: 600px) {
  }
}
