@import '../../../../style/variable.scss';

.gymadress {
  height: 100%;
  width: 100%;
  padding: 25px 0;

  &__wrap {
    width: 90%;
    display: grid;
    grid-template: 1fr / auto auto auto auto;
    align-items: start;
    column-gap: 20px;
  }

  &__tittle {
    p {
      color: rgb(155, 155, 155);
      letter-spacing: 1px;
    }
  }
  &__block {
    padding-top: 15px;
    color: var(--searchBlock);
    display: flex;
    align-items: center;
  }
  &__block_open {
    padding-top: 15px;
  }
  &__icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }

  svg {
    fill: var(--oldRed);
    width: 100%;
    height: 100%;
  }

  &__text {
    p {
      letter-spacing: 1px;
    }
  }

  &__open {
    width: 100px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--oldRed);
    p {
      color: #fff;
    }
  }
  @media (max-width: $mw2 + px) {
    &__wrap {
      width: 100%;
    }
  }
  @media (max-width: $mw3 + px) {
    &__column {
      padding-top: 0px;
    }
    &__wrap {
      row-gap: 20px;
      grid-template: auto auto / 1fr 1fr !important;
    }
  }
  @media (max-width: $mw5 + px) {
    & {
      padding: 15px 0;
    }
    &__block {
      padding-top: 10px;
    }
    &__open {
      width: 80px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var(--oldRed);
    }
    &__wrap {
      row-gap: 10px;
      column-gap: 0px;
      grid-template: auto auto auto auto/ 1fr !important;
    }
    &__icon {
      width: 15px;
      height: 15px;
      margin-right: 10px;
    }
  }
}
