@import 'src/style/variable.scss';

.oneproduct {
  .swiper {
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    //  background: var(--goodsBack);
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper {
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  .swiper-slide {
    background-size: cover;
    background-position: center;
  }

  .mySwiper2 {
    height: 100%;
    width: 100%;
  }

  .mySwiperthumb {
    height: 100%;
    box-sizing: border-box;
    padding: 10px 0;
  }

  .mySwiperthumb .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 1;
  }

  .mySwiperthumb .swiper-slide-active {
    position: relative;
    opacity: 1;
  }

  .loop {
    width: 700px;
    height: 600px;
    display: grid;
    grid-template: 75% 25% / 100%;
  }

  .itemslider__img {
    -webkit-background-size: cover;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
  @media (max-width: $mw2 + px) {
    .loop {
      width: 500px;
      height: 460px;
    }
  }
  @media (max-width: $mw3 + px) {
    .loop {
      width: 400px;
      height: 460px;
    }
  }
  @media (max-width: $mw4 + px) {
    .loop {
      max-width: 100%;
      width: 100%;
      height: 100%;
    }
  }
  @media (max-width: $mw5 + px) {
    .loop {
      grid-template: 75% 30% / 100%;
    }
  }
}
