@import 'src/style/variable.scss';

.reviews {
  &__title {
    padding-bottom: 10px;
    p {
      letter-spacing: 1px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  textarea {
    border: 1px solid var(--searchBlock);
    color: var(--searchBlock) !important;
    background: var(--poblick);
    max-width: 600px;
    width: 100%;
    height: 100px;
    border-radius: 2px;
    padding: 10px;
    outline: none;
  }

  &__btn {
    margin-top: 20px;
    height: 40px;
    width: 160px;
    background: var(--searchBlock);
    p {
      color: var(--poblick) !important;
    }
  }
}

.reviewallready {
  h2 {
    letter-spacing: 1px;
    font-weight: 200;
    text-transform: none;
    color: var(--searchBlock) !important;
  }
}
