@import '../../../../style/variable.scss';

.coachtwo {
  width: 100%;
  height: 100%;

  &__roof {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    margin-bottom: 50px;
  }
  &__floor {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    align-items: flex-end;
  }
  &__body {
    height: 290px;
    width: 40%;
    display: grid;
    grid-template: auto 30px 1fr / 1fr;
    align-items: center;
    color: var(--searchBlock);
    opacity: 0;
    transition: opacity 0.5s ease 0.4s;
    justify-items: end;
  }
  &__title {
    justify-self: center;
    width: 80%;
    align-self: end;
  }
  &__text {
    width: 80%;
    justify-self: center;
    align-self: start;
  }

  &__img {
    height: 100%;
    background-image: url('../../../../img/gym/coach/coach.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transition: width 0.5s ease 0.8s;
    left: 0;
    position: absolute;
    width: 0%;
    border-radius: 2px;
  }

  &__img.two {
    background-image: url('../../../../img/gym/coach/coachdop1.jpg');
  }
  &__img.three {
    background-image: url('../../../../img/gym/coach/coach3_1.jpg');
  }
  &__img.four {
    background-image: url('../../../../img/gym/coach/coachdop2.jpg');
  }
  &__blocktwo {
    width: 100%;
    height: 500px;
    display: grid;
    grid-template: 1fr 1fr 1fr / 40% 20px 40% calc(20% - 20px);
    position: relative;
  }

  &__floor .coachtwo__blocktwo {
    grid-template: 1fr 1fr 1fr / calc(20% - 20px) 40% 20px 40%;
  }

  &__back {
    grid-column: 1/2;
    grid-row: 1/3;
  }

  &__back:last-child {
    grid-column: 3/4;
    grid-row: 2/4;
  }

  &__floor {
    .coachtwo__back {
      grid-column: 4/5;
      grid-row: 1/3;
    }

    .coachtwo__back:last-child {
      grid-column: 2/3;
      grid-row: 2/4;
    }
  }

  &__back {
    width: 5%;
    height: 0%;
    position: absolute;
    background: var(--gray);
    opacity: 0;
    transition: width 0.5s ease 0.7s, height 0.5s ease 0.3s,
      opacity 0.5s ease 0s;
    left: 0;
  }
  &__floor.show &__back,
  &__roof.show &__back {
    height: 100%;
    width: 100%;
    opacity: 1;
  }
  &__floor.show &__img,
  &__roof.show &__img {
    width: 100%;
  }
  &__floor.show &__body,
  &__roof.show &__body {
    opacity: 1;
  }

  @media (max-width: $mw1 + px) {
    &__body {
      height: auto;
      width: 45%;
    }
    &__title {
      width: 100%;
    }
    &__text {
      width: 100%;
      padding-bottom: 20px;
    }

    &__blocktwo {
      grid-template: 1fr 1fr 1fr / 45% 20px 45% calc(10% - 20px);
    }

    &__floor .coachtwo__blocktwo {
      grid-template: 1fr 1fr 1fr / calc(10% - 20px) 45% 20px 45%;
    }
  }
  @media (max-width: $mw3 + px) {
    &__roof,
    &__floor {
      align-items: center;
    }
    &__title {
      text-align: center;
    }
    &__body {
      width: 70%;
    }
    &__blocktwo {
      height: 400px;
      grid-template: 1fr 1fr 1fr / 49% 2% 49%;
    }
  }
  @media (max-width: $mw4 + px) {
  }
  @media (max-width: $mw5 + px) {
    &__roof {
      margin-bottom: 30px;
    }
    &__body {
      height: auto;
      width: 100%;
      grid-template: auto 20px 1fr / 1fr;
    }

    &__title {
      width: 100%;
      padding-bottom: 0px;
    }
    &__text {
      width: 100%;
      padding-bottom: 20px;
    }
    &__blocktwo,
    &__floor .coachtwo__blocktwo {
      width: 100%;
      grid-template: 1fr 10px 1fr / 1fr;
    }
    &__back {
      grid-column: 1/2;
      grid-row: 1/2;
    }

    &__back:last-child {
      grid-column: 1/2;
      grid-row: 3/4;
    }

    &__floor {
      .coachtwo__back {
        grid-column: 1/2;
        grid-row: 1/2;
      }

      .coachtwo__back:last-child {
        grid-column: 1/2 !important;
        grid-row: 3/4 !important;
      }
    }
  }
}
