@import 'src/style/variable.scss';
.grouptrain {
  .swiper {
    width: 100%;
    height: 100%;
    background: #000;
  }

  .swiper-slide {
    font-size: 18px;
    color: #fff;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 80px 70px;
  }

  .parallax-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 130%;
    height: 100%;
    -webkit-background-size: cover;
    background-size: cover;
    background-position: top;
    background-image: url('../../../../../img/Home/infoBlock/child.jpg');
  }

  .swiper-slide .title {
    h1 {
      color: #fff !important;
      text-align: left;
      font-weight: 400;
      text-shadow: 0 0 5px #000;
      text-transform: none;
    }
  }

  .swiper-slide .subtitle {
    h3 {
      padding-bottom: 10px;
      text-shadow: 0 0 5px #000;
    }
  }

  .swiper-slide .text {
    max-width: 400px;
    p {
      line-height: 22px;
      text-shadow: 0 0 5px #000;
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    height: 100% !important;
    top: 0 !important;
    bottom: 0 !important;
    margin-top: 0 !important;

    opacity: 1 !important;
    transition: background 0.5s ease 0s;
    &:before {
      opacity: 0 !important;
    }
    &::after {
      opacity: 0 !important;
    }
  }

  .swiper:hover {
    .swiper-button-next,
    .swiper-button-prev {
      background: rgba(255, 255, 255, 0.207) !important;
      // &:before {
      //   opacity: 0.9 !important;
      // }
      // &::after {
      //   opacity: 0.9 !important;
      // }
    }
    .swiper-button-next:hover,
    .swiper-button-prev:hover {
      background: rgba(255, 255, 255, 0.365) !important;
      // &:before {
      //   opacity: 1 !important;
      // }
      // &::after {
      //   opacity: 1 !important;
      // }
    }
  }

  .swiper-button-next {
    right: 0 !important;
  }

  .swiper-button-prev {
    left: 0 !important;
  }

  .swiper-button-disabled {
    display: none;
  }

  @media screen and (max-width: $mw3 + px) {
  }
  @media screen and (max-width: $mw4 + px) {
    .swiper-slide {
      padding: 50px 60px;
    }

    .swiper-button-next,
    .swiper-button-prev {
      color: #fff !important;
      height: 0 !important;
      background: none !important;
      top: 50% !important;
      margin-top: 0px !important;
      width: 50px !important;
      opacity: 1 !important;

      &::after {
        opacity: 1;
      }
    }
  }
  @media screen and (max-width: 580px) {
    .swiper-slide {
      padding: 30px;
    }
    .swiper-button-next:after,
    .swiper-button-prev:after {
      font-size: 25px !important;
    }

    .swiper:hover {
      .swiper-button-next,
      .swiper-button-prev {
        opacity: 1 !important;
      }
    }
  }

  @media screen and (max-width: 320px) {
    //  .swiper-slide {
    //    padding: 30px 50px;
    //  }
  }
}
