@import '../../../../../style/variable.scss';

.groupTraining {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  background: var(--poblick);

  &__row {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    flex-direction: column;
  }
  &__table {
    width: 100%;
  }
  &__text {
    text-align: center;
    padding: 0 0 calc(20px + (30 - 20) * ((100vw - 320px) / (1100 - 320))) 0;
    color: var(--searchBlock);
  }
  @media screen and (max-width: $mw5 + px) {
    &__table {
      display: none;
    }
  }
}

.firstTrain {
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 0 0 calc(35px + (40 - 35) * ((100vw - 320px) / (1100 - 320))) 0;
  &__btn {
    width: 100px;
  }
  &__input {
    width: 200px;
    margin-right: 20px;
  }
  &__form {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  @media screen and (max-width: $mw3 + px) {
    &__input {
      max-width: 236px;
      margin-right: 10px;
    }
    &__btn {
      width: 100px;
    }
  }
  @media screen and (max-width: 645px) {
    &__input {
      max-width: 208px;
    }
    &__form {
      input {
        max-width: 208px;
      }
    }
  }
  @media screen and (max-width: 581px) {
    &__input {
      max-width: 180px;
    }
    &__form {
      input {
        max-width: 180px;
      }
    }
  }
}
