@import 'src/style/variable.scss';

.stars {
  width: 100%;
  display: flex;
  padding-top: 10px;
  padding-bottom: 20px;
  align-items: center;
  &__items {
    display: flex;
    align-items: center;
    padding-right: 10px;
    height: 20px;
  }
  &__block {
    display: flex;
    align-items: center;
    height: 20px;
  }
  &__count {
    padding-right: 15px;
    display: flex;
    align-items: center;
    p {
      color: var(--searchBlock);
      line-height: 130% !important;
    }
  }
  &__item {
    margin-right: 5px;
    width: 16px;
    height: 16px;
    svg {
      width: 100%;
      height: 100%;
      fill: transparent;
      stroke-width: 10;
      cursor: pointer;
      stroke: var(--searchBlock);
    }

    &.add {
      svg {
        fill: var(--oldyellow);
      }
    }
  }

  &__text {
    height: 20px;
    padding-left: 16px;
    border-left: 1px solid rgb(121, 121, 121);
    display: flex;
    align-items: center;
    p {
      color: rgb(121, 121, 121);
    }
  }

  @media (max-width: $mw2 + px) {
    & {
      height: auto;
      display: grid;
      grid-template: auto auto / 1fr;
    }
    &__item {
      width: 15px;
      height: 15px;
    }
    &__items {
      align-self: end;
    }
    &__text {
      padding-top: 5px;
      align-self: start;
      border: none;
      height: auto;
      padding-left: 0px;
      p {
        line-height: 18px !important;
      }
    }
  }
}

