@import 'src/style/variable.scss';

.oneproduct {
  width: 100%;
  min-height: 100vh;

  &__title {
    h3 {
    }
  }

  &__wrap {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    padding-bottom: 60px;
  }

  &__row {
    width: 100%;
    height: 100%;
  }
  &__pathblock {
    width: 100%;
    display: flex;
    padding-bottom: 30px;
    flex-wrap: wrap;
    .oneproduct__path:last-child {
      padding-right: 23px;
      position: relative;
      p {
        color: rgb(185, 185, 185) !important;
        font-weight: 400;
        cursor: default !important;
      }
    }
  }
  &__path {
    padding-right: 23px;
    position: relative;
    p {
      color: rgb(155, 155, 155);
      font-weight: 400;
      cursor: pointer;
    }
    p:hover {
      color: rgb(185, 185, 185) !important;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 4px;
      height: 1px;
      width: 15px;
      background-color: #c3c2ca;
    }

    &:last-child:before {
      display: none;
    }
  }

  &__item {
    height: 100%;
    display: grid;
    grid-template: 1fr / auto 5% 1fr;
    justify-items: start;
  }

  &__oneblock {
    justify-self: end;
    width: 100%;
    height: 100%;
    background: var(--goodsBack);
    border-radius: 5px;
    padding: 10px;
  }

  &__oneblockbottom {
    width: 100%;
    height: 100%;
    background: var(--bottomItem);
    border-radius: 5px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  &__bottom {
    width: 100%;
    height: 100%;
    padding: 5px;
  }
  &__check {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgba(47, 47, 47, 0.674);
    backdrop-filter: blur(8px);
    opacity: 0;
    visibility: hidden;
    svg {
      width: 30px;
      fill: rgb(255, 255, 255);
    }
  }
  .mySwiperthumb .swiper-slide-thumb-active &__check {
    opacity: 1;
    visibility: visible;
  }

  &__twoblock {
    grid-column: 3/4;
    width: 100%;
    height: auto;
    color: var(--searchBlock);
    display: grid;
    grid-template: auto auto auto auto 50px 1fr / 1fr;
  }
  .product__heart {
    width: 22px;
    height: 22px;
    opacity: 0.8 !important;
  }
  &__img {
    height: 100%;
    justify-content: flex-end;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: transform 1s ease 0s;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    padding: 5px;
  }

  &__price {
    display: flex;
    padding: 20px 0;
    align-items: flex-end;
    &.add {
      p:first-child {
        font-weight: bold !important;
        padding-right: 10px;
      }
      p:last-child {
        text-decoration: line-through;
        opacity: 0.8;
      }
    }
  }

  &__brand {
    padding-top: 5px;
    color: rgb(121, 121, 121);
  }
  &__about {
    align-self: start;
    padding-top: 30px;
  }
  &__add {
    display: grid;
    grid-template: 1fr / 60% 70px;
    width: 100%;
  }
  &__btn {
    border-radius: 5px;
  }

  &__heart {
    margin-left: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--searchBlock);
    border-radius: 2px;
    cursor: pointer;
  }
  @media (max-width: $mw1 + px) {
    &__oneblock {
      width: 100%;
    }
    &__item {
      grid-template: 1fr / auto 5% 1fr;
    }
  }
  @media (max-width: $mw2 + px) {
    &__row {
      width: 100%;
    }
  }
  @media (max-width: $mw3 + px) {
    .product__heart {
      width: 20px;
      height: 20px;
    }
    &__row {
      width: 100%;
    }
    &__add {
      grid-template: 1fr / 60% 70px;
    }
  }

  @media (max-width: $mw4 + px) {
    &__add {
      display: grid;
      grid-template: 1fr / minmax(100px, 330px) 70px;
      width: 100%;
    }
    &__price {
      padding: 15px 0;
    }
    &__item {
      grid-template: 450px 20px auto/ 100%;
      align-items: start;
    }
    &__pathblock {
      padding-bottom: 20px;
    }
    &__oneblock {
      padding: 20px 10px;
    }
    .product__heart {
      width: 20px;
      height: 20px;
      opacity: 1 !important;
    }

    &__twoblock {
      grid-column: 1/2;
      grid-row: 3/4;
      height: auto;
      grid-template: auto auto auto auto 40px 1fr / 1fr;
    }
    &__about {
      padding-top: 20px;
    }
  }

  @media (max-width: 400px) {
    &__item {
      grid-template: 300px 20px auto/ 100%;
      align-items: start;
    }
    &__oneblock {
      padding: 10px;
    }

    &__about {
      p.mini {
        line-height: 20px !important;
      }
    }

    &__check {
      svg {
        width: 20px;
        fill: rgb(255, 255, 255);
      }
    }
  }
}

.about {
  &__text,
  li {
    list-style-type: '-' !important;
    max-width: 400px;
  }
  li {
    padding-left: 5px;
  }
  &__title {
    margin-bottom: 10px;
  }
}
