@import '../../../../../style/variable.scss';

.grouptrain {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  &__items {
    grid-row: 1/2;
    grid-column: 1/2;
    background: var(--oldgray);
    position: relative;
    width: 100%;
    height: 100%;
    cursor: pointer;
  }

  &__items:nth-child(2) {
    grid-row: 1/2;
    grid-column: 2/3;
  }
  &__items:nth-child(3) {
    grid-row: 1/2;
    grid-column: 3/4;
  }
  &__items:nth-child(4) {
    grid-row: 1/2;
    grid-column: 4/5;
    border-right: none;
  }

  &__item {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: top;
    background-size: top;
    background-size: contain;
    background-image: url('../../../../../img/Home/grouptrain/child2.jpg');
  }

  &__item.two {
    background-image: url('../../../../../img/Home/grouptrain/coach2.jpg');
  }
  &__item.three {
    background-image: url('../../../../../img/Home/grouptrain/train2.jpg');
  }
  &__item.four {
    background-image: url('../../../../../img/Home/grouptrain/clobe.jpg');
  }

  &__line {
    display: block;
    height: 2px;
    background: var(--oldyellow);
    width: 16%;
  }
  &__body {
    background: var(--bgcolorLocal);
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease 0s;
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 55%;
    height: 40%;

    h2,
    p {
      color: var(--searchBlock);
      text-align: center;
    }
  }

  &__items:hover &__body {
    opacity: 1;
    visibility: visible;
  }

  @media screen and (max-width: $mw3 + px) {
    &__text {
      width: 80%;
      height: 50%;
    }
    &__item {
      background-position: top;
    }
  }
  @media screen and (max-width: $mw4 + px) {
  }
  @media screen and (max-width: 580px) {
    &__text {
      width: 70%;
      height: 50%;
    }
  }
}

.lookmore {
  background: var(--gray);
  width: 100%;
  height: 250px;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  &__text {
    margin-bottom: 15px;
    max-width: 500px;
    color: var(--searchBlock);
  }
  @media screen and (max-width: 580px) {
    height: auto;
    padding: 20px 0;

    &__background {
      background: var(--gray);
    }
  }
}

.swiper-fixed-width {
  height: 500px;

  @media screen and (max-width: $mw3 + px) {
    height: 400px;
  }
  @media screen and (max-width: $mw4 + px) {
  }
  @media screen and (max-width: 580px) {
    height: 300px;
  }
}

