.winload {
  width: 100%;
  min-height: 100vh;
  top: 0;
  right: 0;
  height: 100%;
  position: fixed;
  background: var(--poblick);
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  display: none;
  opacity: 0;
  visibility: hidden;
  &.add {
    display: block;
    opacity: 1;
    visibility: visible;
  }
}
