@import 'src/style/variable.scss';

.schedule {
  background: var(--gray);

  &__banner {
    height: 500px;
  }
}

.blockvalue {
  display: flex;
  height: 625px;
  &__one,
  &__two {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template: 1fr/ 1fr 100px;
  }
  &__two {
    grid-template: 1fr/ 100px 1fr;
  }
  &__line.first {
    border-top: 1px solid var(--borderforschedul);
  }
  &__line.last {
    border-bottom: 1px solid var(--borderforschedul);
  }

  .daysblock {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background: var(--poblick);
    border-right: 1px solid var(--borderforschedul);
    color: var(--searchBlock);
    &__body {
      display: flex;
      justify-content: center;
      border-bottom: 1px solid var(--borderforschedul);
      align-items: flex-end;
      padding-bottom: 15px;
      width: 100%;
    }
    p {
      padding: 0 2px;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.5s ease 0s;
    }
    &.last {
      border-bottom: 1px solid var(--borderforschedul);
    }
  }
  .daysblock.one {
    .daysblock__body {
      height: 70%;
    }
  }
  .daysblock.two {
    border-right: none;
    .daysblock__body {
      height: 40%;
    }
  }
  .elem.show {
    .daysblock {
      p {
        opacity: 1;
        visibility: visible;
        line-height: 22px !important;
      }
    }
  }
  &__itemone {
    bottom: -60%;
    transition: bottom 0.5s ease 0s;

    .item__body {
      align-items: flex-end;
    }
  }
  .elem.show &__itemone {
    bottom: 0%;
  }

  &__itemtwo {
    top: 60%;
    transition: top 0.5s ease 0s;
  }

  .elem.show &__itemtwo {
    top: 0%;
  }
}

.block {
  position: relative;
  height: 100%;
  width: 100%;
}

.item {
  height: 450px;
  width: 100%;
  position: absolute;
  background: var(--poblick);
  border: 1px solid var(--borderforschedul);
  overflow: hidden;
  &__text {
    width: 100%;
    word-break: break-all;
    text-align: center;
  }
  &__roof {
    width: 100%;
    height: 70%;
    display: flex;
  }
  &__img {
    width: 80%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../../img/schedule/kidsgym.jpg');
    overflow: hidden;
  }
  &.two &__img {
    background-image: url('../../../img/schedule/old.png');
  }
  &.three &__img {
    background-image: url('../../../img/schedule/cross.jpg');
  }
  &.four &__img {
    background-image: url('../../../img/schedule/gymcomp.jpg');
  }
  &.five &__img {
    background-image: url('../../../img/schedule/match.jpg');
  }
  &.six &__img {
    background-image: url('../../../img/schedule/gymindi.jpg');
  }
  &__blockfree {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease 0s;
    transform: scale(2);

    .btnarrow {
      margin-top: 10px;
    }
    p {
      color: #fff !important;
    }
  }
  &__img:hover &__blockfree {
    transform: scale(1);
    opacity: 1;
    visibility: visible;
  }
  &__hour {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../../img/gym/4x4white.png');
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 20px 20px;

    div {
      p {
        color: var(--borderInput);
        line-height: 140%;
        padding-bottom: 15px;
      }
      p:nth-child(2) {
        word-break: break-all;
      }
      p:last-child {
        padding-bottom: 0;
      }
      span {
        color: var(--searchBlock);
        font-weight: bold !important;
      }
    }
  }

  &__body {
    height: 30%;
    padding: 30px 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    h3 {
      color: var(--searchBlock);
      padding-bottom: 5px;
    }
    p {
      color: var(--borderInput);
    }
  }
}

body.changeTheme .item__hour {
  background-image: url('../../../img/gym/4x4.png');
}

@media screen and (max-width: $mw1 + px) {
  .item {
    height: 400px;
    &__img {
      width: 80%;
    }
    &__hour {
      width: 20%;
    }
  }
}
@media screen and (max-width: $mw2 + px) {
  .blockvalue {
    display: flex;
    height: 555px;

    &__one {
      grid-template: 1fr/ 1fr 90px;
    }
    &__two {
      grid-template: 1fr/ 90px 1fr;
    }
  }
  .item {
    height: 370px;
    &__img {
      width: 75%;
    }
    &__hour {
      width: 25%;
      padding: 10px;
      div {
        p {
          padding-bottom: 5px;
        }
        p:last-child {
          padding-bottom: 0;
        }
      }
    }
    &__roof {
      height: 70%;
    }

    &__body {
      padding: 10px;
      height: 30%;
    }
  }
}
@media screen and (max-width: $mw3 + px) {
  .daysblock.one {
    p {
      height: 100% !important;
    }
  }
  .daysblock.two {
    border-right: none;
    p {
      height: 100% !important;
    }
  }
  .blockvalue {
    height: auto;
    flex-direction: column;
    &__one,
    &__two {
      height: 450px;
      grid-template: 80px 1fr/ 1fr;

      .daysblock {
        grid-row: 1/2;
      }
      .block {
        grid-row: 2/3;
      }
    }
    .item {
      border: 1px solid var(--borderforschedul);
      width: 80%;
    }
    .daysblock {
      height: 100%;
      width: 100%;
      align-self: center;
      background: var(--gray);
      border: none;

      .daysblock__body {
        background: var(--poblick);
        width: 200px;
        height: 100% !important;
        padding: 0;
        display: grid;
        grid-template: 1fr / 1fr 1fr;
        border: none;
        p {
          border-left: 1px solid var(--borderforschedul);
          opacity: 1;
          visibility: visible;
          grid-column: 2/3;
          display: flex;
          justify-content: center;
          align-items: flex-end;
          padding-bottom: 10px;
        }
      }
    }
    .daysblock.one {
      border-left: none;
      p {
        border-right: 1px solid var(--borderforschedul);
        border-left: none;
        grid-column: 1/2;
      }
    }
    &__itemtwo {
      right: 0;
    }
    &__itemone {
      bottom: -60%;
      transition: bottom 0.5s ease 0s;
      border-left: 1px solid var(--itemBorder);

      .item__body {
        align-items: flex-end;
      }
    }
    &__itemone {
      bottom: 0%;
    }

    &__itemtwo {
      top: 0%;
    }
  }
}

@media screen and (max-width: $mw5 + px) {
  .blockvalue {
    &__one,
    &__two {
      height: 320px;
      grid-template: 70px 1fr/ 1fr;
    }
    .item {
      width: 100%;
      height: 250px;

      &__img {
        width: 70%;
      }
      &__hour {
        width: 30%;
      }
    }
  }
}
