@import 'src/style/variable.scss';

.schedulebanner {
  width: 100%;
  height: 100%;
  background: var(--gray);
  padding-top: 117px;
  &__row {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template: 150px 300px 150px 100px/ 35% calc(15% + 100px) 1fr;
  }
  &__footer,
  &__roof,
  &__img,
  &__padding,
  &__body {
    background: var(--poblick);
  }
  &__footer {
    grid-row: 4/5;
    grid-column: 1/3;
  }
  &__padding {
    grid-row: 3/4;
    grid-column: 1/4;
  }
  &__img {
    grid-row: 1/3;
    grid-column: 2/4;
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  &__back {
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../../../img/schedule/tventymatch.jpg');
  }
  &__back_two {
    background-image: url('../../../../img/schedule/sparring.jpg');
  }
  &__back_three {
    background-image: url('../../../../img/schedule/sparring2.jpg');
  }
  &__dateline {
    height: 100px;
    width: 100%;
  }
  &__box {
    width: 200px;
    background: var(--poblick);
  }
  &__line {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  &__body {
    width: 100%;
    height: 100%;
    padding: 0 15px;
    color: var(--searchBlock);
    display: grid;
    grid-template: 1fr 1fr auto / 1fr;
  }
  &__title {
    align-self: end;
    padding-bottom: 10px;
    span {
      color: var(--oldRed);
    }
  }
  &__clubs {
    color: var(--borderInput);
    padding-bottom: 15px;
  }
  &__text {
    width: 70%;
  }
  &__body,
  &__roof {
    border-bottom: 1px solid var(--borderforschedul);
  }
  &__date {
    height: 100%;
    width: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../../../img/gym/4x4white.png');
    display: grid;
    grid-template: 1fr / auto 1fr;
    align-items: center;

    &_floor {
      justify-self: start;
      padding-left: 15px;
      p {
        color: var(--searchBlock);
        text-transform: uppercase;
        line-height: 150% !important;
      }
    }

    &_num {
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-left: 15px;
      p {
        color: var(--borderInput);
        font-size: 80px;
        line-height: 78px;
      }
    }
  }
}
body.changeTheme .schedulebanner__date {
  background-image: url('../../../../img/gym/4x4.png');
}
.swiper-schedule {
  height: 100%;
  width: 100%;
  .swiper-pagination {
    bottom: 20px !important;
  }

  .swiper-pagination-bullet {
    height: 10px !important;
    width: 10px !important;
    background: #fff !important;
  }
  .swiper-pagination-bullet-active {
    background: #fff !important;
  }
}

.schedulfloor {
  width: 100%;
  height: 100%;

  &__body {
    color: var(--searchBlock);
    display: flex;
    padding-left: 15px;
    align-items: center;
    height: 100%;
    width: 35%;
    border-bottom: 1px solid var(--borderforschedul);
  }
  &__text {
    margin-right: 20px;
  }
  &__wrap {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  &__people {
    height: 100%;
    display: grid;
    width: calc(15% + 100px);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../../../img/gym/4x4white.png');
  }

  &__block {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    h3 {
      color: var(--borderInput);
      font-size: 75px;
      line-height: 72px;
    }
    p {
      text-transform: uppercase;
      justify-content: start;
      color: var(--searchBlock);
    }
  }

  &__points {
    border-right: 2px solid var(--borderforschedul);
  }
  &__points,
  &__days {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    div:last-child {
      align-self: start;
      padding-left: 15px;
    }
  }
}

@media screen and (max-width: $mw2 + px) {
  .schedulebanner {
    &__row {
      grid-template: 150px 300px 130px 100px/ 35% calc(15% + 90px) 1fr;
    }
    &__title {
      align-self: start;
      padding: 10px 0 0 0;
    }
    &__body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
    }
    &__clubs {
      padding-bottom: 0px;
    }
    &__date {
      width: 50%;
    }
    &__box {
      width: 180px;
    }
    &__back {
      background-position: right;
    }
    &__text {
      width: 100%;
    }
  }
}

@media screen and (max-width: $mw3 + px) {
  .schedulebanner {
    &__row {
      grid-template: 100px 280px 150px 100px/ 1fr;
    }
    &__roof {
      grid-row: 1/2;
      grid-column: 1/2;
      flex-direction: row;
    }

    &__date {
      height: 100%;
      width: 50%;

      &_floor {
        align-self: center;
        padding-left: 15px;
        padding-bottom: 5px;
        p {
          align-items: center;
          text-transform: uppercase;
          padding-left: 0;
        }
      }

      &_num {
        p {
          font-size: 80px;
          line-height: 82px;
        }
      }
    }
    &__footer {
      display: none;
    }
    &__padding {
      grid-row: 4/5;
      grid-column: 1/2;
    }
    &__img {
      grid-row: 2/3;
      grid-column: 1/2;
    }
    &__dateline {
      justify-content: flex-start;
    }

    &__box {
      width: calc(50% + 100px);
    }
    &__line {
      width: 100%;
      justify-content: flex-start;
    }
    &__text {
      display: none;
    }
    &__body {
      justify-content: space-evenly;
    }
    &__back {
      background-position: center;

      &_one {
        background-image: url('../../../../img/schedule/match_mob.jpg');
      }
      &__two {
        background-position: bottom !important;
      }
    }
  }

  .schedulfloor {
    &__wrap {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    &__block {
      h3 {
        color: var(--borderInput);
        font-size: 60px;
        line-height: 62px;
      }
      p {
        align-items: start;
        text-transform: uppercase;
        justify-content: start;
        color: var(--searchBlock);
      }
    }
    &__body {
      color: var(--searchBlock);
      display: flex;
      padding-left: 15px;
      align-items: center;
      height: 100%;
      width: 50%;
    }
    &__text {
      margin-right: 20px;
    }
    &__wrap {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    &__people {
      width: 50%;
    }
  }
}
@media screen and (max-width: $mw4 + px) {
  .schedulebanner {
    padding-top: 100px;
  }
}

@media screen and (max-width: $mw5 + px) {
  .schedulebanner {
    &__row {
      grid-template: 80px 200px 120px 60px/ 1fr;
    }
    &__img {
    }
    &__dateline {
      height: 50px;
    }
    &__date {
      height: 100%;
      width: 100%;
      grid-template: 1fr / auto auto 1fr;

      &_floor {
        grid-column: 2/3;
        grid-row: 1/2;
        align-self: end;
        padding-left: 15px;
        padding-bottom: 10px;
      }

      &_num {
        grid-column: 1/2;
        grid-row: 1/2;
        padding-left: 15px;
        p {
          font-size: 75px;
          line-height: 82px;
        }
      }
    }
    &__back {
      background-position: center;
      &_one {
        background-image: url('../../../../img/schedule/tventymatch.jpg');
      }
    }

    .swiper-schedule {
      .swiper-pagination {
        bottom: 10px !important;
      }

      .swiper-pagination-bullet {
        height: 8px !important;
        width: 8px !important;
      }
    }
  }
  .schedulfloor {
    &__wrap {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    &__body {
      color: var(--searchBlock);
      display: flex;
      padding-left: 15px;
      align-items: center;
      height: 100%;
      width: 100%;
    }
    &__people {
      display: none;
    }
  }
}
