@import '../../../../style/variable.scss';

.gym {
  width: 100%;
  height: 100%;
  background: var(--gray);
  padding-top: 117px;

  &__wrap {
    height: 100%;
    min-height: 100%;
    display: grid;
    grid-template: 350px 1fr / 1fr;
  }

  &__blockone {
    width: 100%;
    background: #000;
    position: relative;
    text-align: left;
  }

  &__oneimage {
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    width: 40%;
    position: absolute;
    right: 0;
    top: 0;
  }
  &__oneback {
    height: 100%;
    width: 100%;
    transform: translateX(-20%);
    background: linear-gradient(to right, #000 18%, transparent);
  }
  &__onewrap {
    display: grid;
    grid-template: auto 1fr 1fr / 1fr;
    width: 100%;
    height: 100%;
    padding: 25px 0;
  }
  &__pathblock {
    width: 100%;
    display: flex;
  }

  &__path {
    padding-right: 23px;
    position: relative;
    p {
      color: rgb(155, 155, 155);
      font-weight: 400;
      cursor: pointer;
    }
    p:hover {
      color: rgb(185, 185, 185);
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 4px;
      height: 1px;
      width: 15px;
      background-color: #c3c2ca;
    }

    &:last-child:before {
      display: none;
    }
  }
  &__onetitle {
    width: 35%;
    color: #fff;
    padding-top: 20px;
    white-space: pre-wrap;
    h2 {
      text-transform: none;
      line-height: 50px;
      span {
        white-space: nowrap;
      }
    }
  }
  &__btnblock {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    padding-top: 40px;
  }
  &__btn {
    max-width: 200px;
    width: 100%;
  }
  &__btn:first-child {
    margin-right: 20px;
  }
  &__btn:last-child {
    max-width: 120px;
    width: 100%;
  }
  @media (max-width: $mw3 + px) {
    & {
      padding-top: 108px;
    }
    &__onewrap {
      grid-template: auto 1fr auto / 1fr;
    }
    &__btnblock {
      padding-top: 0px;
    }
  }
  @media (max-width: $mw4 + px) {
    & {
      padding-top: 100px;
    }
    &__wrap {
      grid-template: 300px auto / 1fr;
    }
    &__blockone {
      height: 300px;
    }
    &__onewrap {
      grid-template: auto 1fr auto / 100%;
      width: 100%;
    }
    &__oneimage {
      display: none;
    }
    &__onetitle {
      width: 70%;
    }
  }
  @media (max-width: $mw5 + px) {
    &__onetitle {
      h2 {
        line-height: 40px;
      }
    }
    &__onewrap {
      padding: 15px 0 25px 0;
    }
    &__pathblock {
      flex-wrap: wrap;
    }
    &__path {
      margin-top: 5px;
    }

    &__btn:last-child {
      max-width: 100px;
      width: 100%;
    }
  }
}
