@import 'src/style/variable.scss';

.banner {
  height: 100vh;
  position: relative;
  width: 100%;
  overflow: hidden;
  &__img {
    background-image: url('../../../../../img/Home/BannerHead.jpg');
    position: absolute;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    animation: scaleBlock 10s ease 0s 1 both;
  }
  @keyframes scaleBlock {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(1.2);
    }
  }

  &__ground {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-image: var(--homeBanner);
  }
  &__menu {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__body {
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin-right: 10px;
  }

  &__title span {
    position: relative;
    transition: all 0.5s ease 0.3s;
    color: #fff !important;
    line-height: 110%;
    letter-spacing: 1px;
    text-align: center;
    width: 100%;
    font-size: calc(50px + (75 - 50) * ((100vw - 320px) / (1100 - 320)));
    user-select: none;
    text-align: left;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    font-family: var(--fontTitle);
    text-transform: uppercase;
  }

  &__title span:nth-child(1),
  &__title span:nth-child(3) {
    right: -150%;
  }

  &__title span:nth-child(2) {
    display: block;
    left: -100%;
    color: transparent !important;
    -webkit-text-stroke: 1px #fff;
  }

  &__title.add span:nth-child(2) {
    left: 0%;
  }

  &__title.add span:nth-child(1),
  &__title.add span:nth-child(3) {
    right: 0%;
  }

  &__youtube {
    height: 100%;
    width: 100%;
    opacity: 0.6;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 2%;
    background-position: 0 0;
    background-size: cover;
  }
  .fa-youtube {
    font-size: 50px;
    color: rgba(7, 7, 7, 0.712);
  }

  @media (max-width: $mw2 + px) {
    & {
      height: 100vh;
    }
  }
  @media (max-width: $mw3 + px) {
    & {
      height: 100vh;
    }
  }
  @media screen and (max-width: $mw4 + px) {
    & {
      height: 80vh;
    }
    &__ground {
      width: 100%;
      height: 100%;
    }

    &__title span,
    &__title {
      text-align: center;
    }
  }
}
