@import 'src/style/variable.scss';

.comments {
  width: 100%;

  &__items {
    display: flex;
    width: 100%;
    padding-bottom: 10px;
    user-select: none;
  }

  &__item {
    padding-right: 20px;
    cursor: pointer;
    p {
      opacity: 0.6;
      color: var(--searchBlock);
      &:hover {
        opacity: 1;
      }
    }

    &.check {
      p {
        opacity: 1 !important;
      }
    }
  }

  &__block {
    padding-top: 40px;
    max-width: 100%;
    p {
      color: var(--searchBlock);
      padding-bottom: 5px;
    }
    p:first-child {
      padding-bottom: 20px;
    }
    p:last-child {
      padding-bottom: 0px;
    }
  }

  &__course {
    width: 100%;
    height: 2px;
    background: var(--sortBlock);
  }
  &__line {
    height: 100%;
    background: var(--searchBlock);
    transition: transform 0.2s linear 0s;
  }
  @media (max-width: 400px) {
    &__block {
      padding-top: 20px;
    }
  }
}

.details {
  max-width: 700px;
  width: 100%;
}
