@import '../../../../../style/variable.scss';

.ourgoods {
  width: 100%;

  &__elem {
    height: 600px;
    width: 100%;
    background-image: url('../../../../../img/Home/shop/shop2.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: -10% center;
  }
  &__container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template: 1fr / 50% 50%;
    align-items: center;
    color: var(--searchBlock);
  }

  &__body {
    grid-column: 2/3;
    justify-self: center;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__blocktext {
    width: 55%;
    height: 100%;
  }
  h2 {
    padding-bottom: 10px;
  }
  &__btn {
    margin-top: 15px;
  }
  &__img {
    width: 100%;
    height: 400px;
    border-radius: 2px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.735);
    background-image: url('../../../../../img/Home/shop/Gif.gif');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: left center;
  }

  @media (max-width: $mw3 + px) {
    &__elem {
      height: 650px;
      background-size: 400px 400px;
      background-position: 0% top;
    }
    &__container {
      grid-template: 400px 1fr/ 1fr;
    }
    &__img {
      grid-row: 1/2;
      grid-column: 1/2;
      height: 350px;
      background-position: center;
    }
    &__body {
      grid-row: 2/3;
      grid-column: 1/2;
      padding-top: 30px;
      align-self: flex-start;
    }
    &__blocktext {
      width: 100%;
    }
  }

  @media (max-width: $mw4 + px) {
    &__elem {
      height: auto;
      background-size: 300px 300px;
      background: none;
    }
    &__container {
      grid-template: 300px 1fr/ 1fr;
    }
    &__img {
      height: 300px;
    }
    &__body {
      padding-top: 30px;
    }
  }

  @media (max-width: $mw5 + px) {
    &__container {
      grid-template: 200px 1fr/ 1fr;
    }
    &__img {
      height: 200px;
    }
    &__body {
      padding-top: 15px;
    }
  }
}
