@import '../../../style/variable.scss';

.contacts {
  width: 100%;
  height: 100%;

  &__wrap {
    width: 100%;
    height: 100%;
  }

  &__item {
    height: 140px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__block {
    height: 100%;
    width: 80%;
    border-top: 1px solid var(--borderforschedul);
    display: flex;
    justify-content: center;
    align-items: center;
    &:last-child {
      border-bottom: 1px solid var(--borderforschedul);
    }
  }

  &__contain {
    height: 80px;
    width: 100%;
    display: grid;
    grid-template: 1fr/ 1fr 35% auto;
    color: var(--searchBlock);
    align-items: center;
  }
  &__blocktwo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  &__number {
    display: flex;
    align-items: center;
    padding-top: 20px;
  }

  &__blockthree {
  }

  &__more {
    display: none;
    padding-right: 10px;
  }

  &__circle {
    position: relative;
    width: 40px;
    height: 30px;
  }

  &__arrow {
    left: 0;
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 1px solid var(--oldRed);
    color: var(--oldRed);
    display: flex;
    justify-content: center;
    align-items: center;
    animation: above 1s ease-in 0s infinite alternate-reverse both;
    cursor: pointer;
  }

  &__title {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__titleitem {
    width: 80%;
    height: 100px;
    display: flex;
    align-items: center;
    color: var(--searchBlock);
  }

  &__work {
    display: flex;
    align-items: center;
    padding-top: 20px;
  }

  &__icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }

  &__adress {
    display: flex;
    align-items: center;
  }

  svg {
    fill: var(--oldRed);
    width: 100%;
    height: 100%;
  }
  @keyframes above {
    0% {
      left: 0;
    }
    100% {
      left: 10px;
    }
  }
  &__item:hover {
    //  background: var(--oldwhite);
  }
  @media (max-width: $mw3 + px) {
    &__item {
      height: 150px;
    }
    &__block {
      width: 100%;
    }
    &__contain {
      height: auto;
      grid-template: 1fr/ 1fr 40% auto;
    }
    &__titleitem {
      width: 100%;
    }
  }
  @media (max-width: 640px) {
    &__blocktwo {
      align-items: flex-end;
    }
    &__blocktwobody {
      width: 300px;
    }
    &__item {
      height: 150px;
    }
    &__block {
      width: 100%;
    }
    &__contain {
      height: auto;
      grid-template: 1fr auto / 1fr 1fr;
      justify-items: stretch;
    }
    &__work {
      padding-top: 15px;
    }
    &__number {
      padding-top: 15px;
    }
    &__blockthree {
      grid-column: 1/3;
      justify-self: end;
      height: 40px;
      display: flex;
      align-items: flex-end;
    }

    &__titleitem {
      width: 100%;
    }
  }

  @media (max-width: 640px) {
    &__blocktwo {
      grid-row: 2/3;
      grid-column: 1/2;
      align-items: start;
      padding-top: 15px;
    }
    &__blocktwobody {
      width: auto;
    }
    &__item {
      height: auto;
    }
    &__block {
      width: 100%;
    }
    &__contain {
      height: auto;
      grid-template: auto auto auto / 1fr;
      justify-items: stretch;
      padding: 15px 0;
    }
    &__blockthree {
      grid-column: 1/2;
      grid-row: 3/4;
      justify-self: start;
      height: 40px;
      display: flex;
      align-items: flex-end;
    }

    &__titleitem {
      width: 100%;
      height: 60px;
    }

    &__more {
      display: flex;
      height: 25px;
      align-items: center;
      padding-left: 30px;
    }
    &__circle {
      width: 40px;
      height: 25px;
    }
    &__arrow {
      width: 25px;
      height: 25px;
    }
  }

  @media (max-width: $mw5 + px) {
    &__titleitem {
      height: 60px;
      align-items: end;
      padding-bottom: 15px;
    }

    &__more {
      height: 20px;
      padding-left: 25px;
    }
    &__circle {
      width: 35px;
      height: 20px;
    }
    &__arrow {
      width: 20px;
      height: 20px;
    }
    &__icon {
      width: 15px;
      height: 15px;
      margin-right: 10px;
    }
  }
}
