@import 'src/style/variable.scss';

.product__item {
  .swiper {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    text-align: center;
    background: transparent;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    padding-bottom: 0px;
  }

  .product__img {
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: transform 1s ease 0s;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }
  .swiper-pagination {
    bottom: 0px !important;
    left: 0 !important;
    right: auto !important;
    width: auto !important;
    background: rgba(57, 57, 57, 0.544);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 18px;
    padding: 0 5px;
    border-radius: 10%;
    opacity: 0 !important;
  }
  .SwiperProduct.notpagination .swiper-pagination {
    display: none;
  }
  .swiper-pagination-bullet-active {
    background-color: #ffff !important;
    opacity: 0 !important;
  }
  .swiper-pagination-bullet {
    background-color: #ffff !important;
    opacity: 0;
    cursor: pointer;
    height: 5px;
    width: 5px;
    margin: 0 3px !important	;
  }

  &:hover {
    .swiper-pagination-bullet-active {
      background-color: #ffff !important;
      opacity: 1 !important;
    }
    .swiper-pagination {
      opacity: 1 !important;
    }
    .swiper-pagination-bullet {
      background-color: #ffff !important;
      opacity: 0.5;
    }
  }
  @media (max-width: $mw3 + px) {
    .swiper-pagination-bullet-active {
      background-color: #ffff !important;
      opacity: 1 !important;
    }
    .swiper-pagination {
      opacity: 1 !important;
    }
    .swiper-pagination-bullet {
      background-color: #ffff !important;
      opacity: 0.5;
    }
  }
  @media (max-width: $mw5 + px) {
    .swiper-pagination {
      height: 12px;
      padding: 0 2px;
      opacity: 1 !important;
    }
    .swiper-pagination-bullet {
      height: 3px;
      width: 3px;
    }
  }
}
