@import '../../../../../style/variable.scss';

.titleFirst {
  padding: 0 0 0px 0;
  width: 100%;
  background: var(--poblick);
  &__row {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    flex-direction: column;
  }
  &__text {
    text-align: center;
    padding: calc(45px + (50 - 45) * ((100vw - 320px) / (1100 - 320))) 0
      calc(35px + (40 - 35) * ((100vw - 320px) / (1100 - 320))) 0;
  }
  &__title {
    position: relative;
    padding: 20px 0;
    width: 100%;
    background: linear-gradient(to right, #ffee00, #f7ca18);
    transition: transform 0.5s ease 0s;
    display: flex;
    transform: translateX(100%);
    overflow: hidden;
    justify-content: center;
    display: flex;
    align-items: center;
    user-select: none;
    margin-bottom: calc(35px + (60 - 35) * ((100vw - 320px) / (1100 - 320)));
    color: var(--oldgray);
  }

  @media screen and (max-width: $mw + px) {
    &__title {
      padding-right: 10px;
      padding: 10px 0 10px 0;
    }
  }

  @media screen and (max-width: $mw4 + px) {
    &__title {
      padding: 10px 0;
      text-align: center;
    }
    &__form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 0 0 60px 0;
      input {
        width: 100%;
        margin: 0;
        margin-bottom: 20px;
      }
      &_btn {
        width: 100%;
      }
    }
  }
}
