@import '../../../../style/variable.scss';

.program {
  background: var(--gray);
  &__wrap {
    width: 100%;
    color: var(--searchBlock);
  }
  &__blockone {
    width: 100%;
    height: 100%;
    display: flex;
    margin-bottom: 50px;
  }

  &__body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 450px;
  }

  &__title {
    position: relative;
    height: 70%;
    top: 10px;
    overflow: hidden;
    &.img {
      right: 0;
      left: auto;
    }
  }

  &__halftitle {
    left: -10%;
    position: absolute;
    width: 100%;
    transition: transform 1s ease 0s;
    transform: matrix(1, 0, 0, 1, 0, 0);
    h1 {
      font-weight: 400 !important;
      line-height: 110%;
      text-transform: none;
      user-select: none;
    }
  }

  &__halftitle.img {
    left: 90%;
    top: 0;

    h1 {
      color: #fff;
      text-shadow: 0 0 2px #000;
      color: var(--red);
    }
  }

  &__aboutus {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &__text {
    width: 60%;
  }

  &__blockimg {
    height: 350px;
    width: calc(50% - 10px);
    position: relative;
    border-radius: 2px;
  }
  &__img {
    border-radius: 2px;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-image: url('../../../../img/gym/3.jpg');
    background-repeat: no-repeat;
    background-position: center 50%;
    transition: width 0.5s ease 1.2s;
  }
  &__img.two {
    background-image: url('../../../../img/gym/5.jpg');
  }
  &__img.three {
    background-image: url('../../../../img/gym/23.jpg');
  }

  &__back {
    width: 100%;
    height: 100%;
    position: absolute;
    background: var(--gray);
    transition: width 0.5s ease 0.7s, height 0.5s ease 0.3s,
      opacity 0.5s ease 0s;
    right: 0;
  }

  &__blocktwo {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template: 1fr/ 1fr 20px 1fr;

    .program__blockimg {
      grid-column: 1/2;
      width: 100%;
    }
    .program__blockimg:last-child {
      grid-column: 3/4;
      width: 100%;
    }
  }

  @media (max-width: $mw2 + px) {
  }
  @media (max-width: $mw3 + px) {
    &__blockimg {
      height: 320px;
    }
    &__title {
      height: 50%;
    }
    &__aboutus {
      height: 50%;
    }
    &__text {
      padding-left: 20px;
      width: 100%;
    }
    &__blockone {
      margin-bottom: 20px;
    }
  }

  @media (max-width: $mw4 + px) {
    &__title {
      position: static;
      height: 100%;
      top: 0px;
    }

    &__halftitle.two {
      display: none;
    }
    &__halftitle.img {
      height: 100%;
      left: 0;
      width: 100%;
      position: static;
      display: flex;
      justify-content: center;
      align-items: center;
      h1 {
        text-align: center;
      }
    }
    &__body {
      width: 100%;
      height: auto;
      margin-top: 20px;
      margin-bottom: 30px;
    }
    &__text {
      padding: 0;
      width: 100%;
    }
    &__blockimg {
      width: 100%;
      height: 250px;
    }
    &__blockone {
      margin-bottom: 0px;
      flex-direction: column;
    }

    &__blocktwo {
      height: 100%;
      width: 100%;
      display: grid;
      grid-template: 1fr 20px 1fr / 1fr;

      .program__blockimg {
        grid-column: 1/2;
        width: 100%;
      }
      .program__blockimg:last-child {
        grid-row: 3/4;
        grid-column: 1/2;
        width: 100%;
      }
    }
  }
}
