@import '../../../../../style/variable.scss';

@media screen and (max-width: 1004px) {
  .menu {
    z-index: 4;

    &__bg {
      text-align: center;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      &:before {
        content: '';
        display: inline-block;
        height: 50%;
        vertical-align: middle;
      }
    }
    &__container {
      display: inline-block;
    }
    &__body {
      height: 100%;
    }
    &__burder {
      order: 0 !important;
    }
    &__secondBlock {
      order: 2;
      align-items: flex-start;
      width: 100%;
      display: flex;
      justify-content: center;

      &_link {
        width: 40px;
        margin: 0 2%;
        display: flex;
        justify-content: center;
        svg {
          width: 30px;
          fill: #fff;
        }
      }
    }
    &__link {
      order: 1;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: auto;
      margin: 10% 0 10% 0;
    }
    &__profily {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
      height: 81px;
    }
  }
}
@media (max-width: $mw4 + px) {
  .menu {
    &__profily {
      height: 65px;
    }
  }
}
@media screen and (max-width: 420px) {
  .menu {
    &__link {
      margin: 30% 0 30% 0;
    }
    &__secondBlock {
      &_link {
        width: 25px;
        margin: 0 12.5px;
      }
    }
  }
}
