@import 'src/style/variable.scss';

.posts {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
  &__item {
    height: auto;
    width: 100%;
    display: flex;
  }

  &__notfound {
    h2 {
      letter-spacing: 1px;
      font-weight: 200;
      text-transform: none;
      color: var(--searchBlock) !important;
    }
  }

  &__block {
    height: 100%;
    max-width: 600px;
    width: 100%;
    box-shadow: 0px 0 5px rgba(0, 0, 0, 0.13);
    padding: 15px;
    margin-bottom: 15px;
  }

  &__head {
    display: grid;
    grid-template: 1fr / auto auto 1fr;
    align-items: center;
  }

  &__rating {
    display: flex;
    align-self: center;
    justify-self: end;
  }

  &__img {
    grid-column: 1/2;
    grid-row: 1/3;
    width: 60px;
    height: 60px;
    background-color: gray;
    border-radius: 100%;
    margin-right: 20px;
  }
  &__name {
    padding-right: 20px;
  }
  &__name,
  &__date {
    p {
      color: var(--searchBlock) !important;
      user-select: none;
    }
  }

  &__date {
    p {
      opacity: 0.6;
    }
  }

  &__icons {
    width: 100%;
    display: flex;
    padding-top: 30px;
    justify-content: space-between;
  }

  &__icon {
    padding-right: 0px;
  }

  &__change {
    height: 20px;
    align-items: center;
    display: flex;
  }

  &__text {
    padding-top: 20px;
    p {
      white-space: pre-wrap;
    }
  }

  &__thumbs {
    display: flex;
  }

  &__like {
    display: grid;
    grid-template: 1fr / auto 30px;
    align-items: center;
  }

  &__like:last-child {
    padding-left: 30px;
    svg {
      bottom: -2px;
    }
  }

  &__countlike {
    justify-self: end;
    p {
      line-height: 130% !important;
      user-select: none;
    }
  }
  &__basket {
    margin-right: 20px;
  }
  &__svg {
    position: relative;
    width: 16px;
    height: 16px;
    svg {
      position: absolute;
      cursor: pointer;
      width: 100%;
      height: 100%;
      fill: var(--searchBlock);
      opacity: 0.4;
      &:hover {
        opacity: 1;
      }
    }
  }
  &__like.add {
    svg {
      opacity: 1 !important;
    }
  }
  &__svg.posts__reload,
  &__svg.posts__basket {
    width: 18px;
  }

  @media (max-width: $mw4 + px) {
    &__svg {
      width: 16px;
      height: 16px;
    }
    &__item {
      max-width: 100%;
    }
    &__svg.posts__reload,
    &__svg.posts__basket {
      width: 15px;
    }
  }
}

.reload {
  width: 100%;
  &__form {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    width: 100%;
  }

  textarea {
    border: 1px solid var(--searchBlock);
    color: var(--searchBlock) !important;
    background: var(--poblick);
    max-width: 600px;
    width: 100%;
    height: 100px;
    border-radius: 2px;
    padding: 10px;
    outline: none;
    font-size: 16px;
    font-weight: 400;
    overflow-y: auto;
  }

  &__btn {
    height: 40px;
    width: 160px;
    background: var(--searchBlock);
    p {
      color: var(--poblick) !important;
    }
  }
  &__cancel {
    height: 40px;
    width: 100px;
    border: 1px solid var(--searchBlock);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
    cursor: pointer;
    p {
      color: var(--searchBlock) !important;
    }
  }
  &__footer {
    max-width: 600px;
    align-items: center;
    justify-content: flex-start;
    display: flex;
    padding: 15px 0 30px 0;
  }
  @media (max-width: $mw4 + px) {
    textarea {
      font-size: 14px;
    }
  }
}
